.snotify {
  display: block;
  position: fixed;
  width: 300px;
  z-index: 9999;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  pointer-events: none;
}
.snotify * {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
.snotify-leftTop,
.snotify-leftCenter,
.snotify-leftBottom {
  left: 10px;
}
.snotify-rightTop,
.snotify-rightCenter,
.snotify-rightBottom {
  right: 10px;
}
.snotify-centerTop,
.snotify-centerCenter,
.snotify-centerBottom {
  left: calc(50% - 300px / 2);
}
.snotify-leftTop,
.snotify-centerTop,
.snotify-rightTop {
  top: 10px;
}
.snotify-leftCenter,
.snotify-rightCenter,
.snotify-centerCenter {
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
